.row.row-space-2 {
  margin: 0 -1px;
}
.m-b-2 {
  margin-bottom: 2px!important;
}
.embed-responsive {
  position: relative;
  display: block;
  width: 100%;
  padding: 0;
  overflow: hidden;
}
.row.row-space-2>[class*=col-] {
